input[type=range]{
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    background: #e0e0e0;
    outline:none;
    border-radius: 15;
    height: 15;
    box-shadow: 5px 5px 10px #bebebe, -5px -5px 10px #ffffff, inset 2px 2px 5px #bebebe, inset -2px -2px 5px #ffffff;
    overflow: hidden;

}

input[type=range]::-webkit-slider-thumb{
    appearance: none;
    -webkit-appearance: none;
    width: 15;
    height: 15;
    border-radius: 50%;
    border: none;
    cursor: pointer;
}
input[type=range]::-moz-range-thumb{
    appearance: none;
    -moz-appearance: none;
    width: 15;
    height: 15;
    border-radius: 50%;
    border: none;
    cursor: pointer;
}

input[type=range].red::-webkit-slider-thumb{
    box-shadow: -407px 0 100px 300px rgba(0, 0, 0, 0.3), 5px 5px 10px #bebebe, -5px -5px 15px rgba(255, 255, 255, 0.9), -407px 0 0 400px red;
}
input[type=range].green::-webkit-slider-thumb{
    box-shadow: -407px 0 100px 300px rgba(0, 0, 0, 0.3), 5px 5px 10px #bebebe, -5px -5px 15px rgba(255, 255, 255, 0.9), -407px 0 0 400px green;
}
